body {
  font-family: 'Courier New', Courier, monospace;
  margin: 20px;
  padding: 0;
  background-color: #D9E5D6;
  color: #99B2DD;
  -ms-flex-pack: center;
      justify-content: center;
}

.square {
  background: #F9DEC9;
  border: 5px solid #E9AFA3;
  float: left;
  font-size: 5rem;
  font-weight: bold;
  line-height: 34px;
  height: 10rem;
  padding: 0;
  text-align: center;
  width: 10rem;
  color: #E9AFA3;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.status {
  margin-bottom: 30px;
  font-size: 50px;
  text-align: center;
}
.game {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

/* .game-info {
  display: flex;
  flex-direction: row;
} */

button {
  background-color: #99B2DD;
  border: 1px solid #3A405A;
  color: #3A405A;
  border-radius: 4px;
  margin: 1rem;
  padding: 0.5rem;
}

ol {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

li {
  margin: 1rem;
}

.game-board {
  background-color: #e8eef8;
  padding: 2rem;
  border-radius: 5px;
  min-width: 580px;
}


